/* === Tables Themes === */

$title-color: #242526;
$total-score-color: #56b258;
$users-score-color: #3b3c3e;
$main-color: #56b258;

// Clearfix mixin
@mixin clearfix() {
    &:before, &:after {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
    }
}

.rwp-table-wrap {
    overflow:hidden;
    margin: 30px 0;
    
    // Title
    .rwp-table-title {
        font-size: 18px;
        font-weight: bold;
        display: block;
        margin-bottom: 20px;
    }
    
    // Legend
    .rwp-legend {
        float:left;
        margin-right: 30px;
        padding: 36px 5px 0 0;

        .rwp-criteria {
            margin-bottom: 10px;
            line-height: 16px;
        }
        
        .rwp-key {
            background: $main-color;
            width: 20px;
            height: 20px;
            display: inline-block;
            vertical-align: middle;
            -webkit-border-radius: 20px;
            border-radius: 20px;
            text-align: center;
            color:#fff;
            font-weight: bold;
            font-size: 12px;
            line-height: 19px;
            margin-right: 5px;
        }
    }
    
    // Generic table
    .rwp-table {
        min-width: 140px;
        float:left;
        margin: 0 0 40px 0;
        
        .rwp-title {
            background: $title-color;
            color: #fff;
            padding: 10px 5px;
            text-align:center;
            font-size:12px;
            font-weight: bold;

            a {
                color: #fff;
                text-decoration: none;
                border: none;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
        
        // Scores
        .rwp-scores-wrap{
            overflow: hidden;
        }
        
        .rwp-total-score-wrap {
            background: $total-score-color;
            float:left;
            width: 50%;
            color: #fff;
            text-align: center;
            padding: 8px 0;
            
            .rwp-total-score {
                display:block;
                font-size: 18px;
                font-weight: bold;
                line-height:18px;
                margin-bottom:5px;
            }
            
            .rwp-total-score-label {
                font-size:12px;
                font-weight:500;
                display:block;
                line-height:12px;
            }
        }
        
        .rwp-users-score-wrap {
            @extend .rwp-total-score-wrap;
            background: $users-score-color;
            
            .rwp-users-score {
                @extend .rwp-total-score;
            }
            
            .rwp-users-score-label {
                @extend .rwp-total-score-label;
            }   

            &.rwp-ur {
                width: 100%;
            }
        }
        
        // Criterias
        .rwp-criterias {
            border: 1px solid #eee;
            border-top: 0;
        }
        
        // Labels
        .rwp-criterias-labels-wrap {
            overflow: hidden;
            margin: 5px 0 0 1px;
            
            .rwp-criteria-label {
                background: $main-color;
                width: 20px;
                height: 20px;
                display: block;
                float: left; 
                margin-left: 14px;
                -webkit-border-radius: 20px;
                border-radius: 20px;
                text-align: center;
                color:#fff;
                font-weight: bold;
                font-size: 12px;
                line-height: 19px;
            }
        }
    }
    
    
    
    // === Table Theme 1 === 
    
    &.rwp-theme-1 {
        
        .rwp-criterias {
            overflow: hidden;
        }

        $barWidth: 24px;
        
        .rwp-criteria-wrap {
            float: left;
            margin-left: 10px;
            width: $barWidth;
            height:170px;
            
            &:last-child {
                margin-right: 10px;
            }
            
            .rwp-criteria-bar {
                background: #eee;
                width:$barWidth;
            }
            
            .rwp-criteria-bar-value {
                width: $barWidth;
                background: $main-color;
                -webkit-box-shadow: inset -2px 0px 0px 0px rgba(0, 0, 0, 0.4);
                box-shadow: inset -2px 0px 0px 0px rgba(0, 0, 0, 0.4);
                
                .rwp-criteria-score {
                    color: #fff;
                    width: $barWidth;
                    display: block;
                    font-weight: bold;
                    font-size: 11px;
                    text-align: center;
                }
            }
        }
    }
    
    
    // === Table Theme 2 ===
    
    &.rwp-theme-2 {
        
        .rwp-criteria-label {
            display:block;
            font-size: 14px;
            font-weight:500;
            margin-bottom: 2px;
        }
        
        .rwp-criteria-wrap {
            padding: 5px;
            border-bottom: 1px solid #eee;
            
            &:last-child {
                border:0;
            }
        }

        $iconSize: 16px;

        .rwp-str {
            @include clearfix;
            //margin-right: 120px;

            .rwp-s {
                float: left;
                display: block;
                width: ($iconSize / 2);
                height: $iconSize;
                background-image:  url(../images/rating-star.png);
                background-repeat: no-repeat;
                background-size: $iconSize;
                margin: 0;
                padding: 0;


                &.rwp-o.rwp-x {
                    background-position: 0px (-$iconSize);
                }

                &.rwp-o.rwp-f {
                    background-position: 0px 0px;
                }

                &.rwp-e.rwp-x {
                    background-position: (-($iconSize / 2)) (-$iconSize);
                }

                &.rwp-e.rwp-f {
                    background-position: (-($iconSize / 2)) 0px;
                }
            }
        }
    }
    
    // === Table Theme 3 ===
    
    &.rwp-theme-3 {
        
        .rwp-criterias {
            overflow: hidden;
        }
        
        .rwp-criteria-label {
            display:block;
            font-size: 12px;
            font-weight:500;
            margin-top: 2px;
            text-align: center;
        }
        
        .rwp-criteria-wrap {
            padding: 5px 0;
            //border-bottom: 1px solid #eee;
            width:50%;
            float:left;
            
            div {
                margin: 0 auto;
            }
        }
    }
    
    // === Table Theme 4 === 
    
    &.rwp-theme-4 {
        
        .rwp-criterias {
            overflow: hidden;
        }

        $barWidth: 24px;
        
        .rwp-criteria-wrap {
            float: left;
            margin-left: 10px;
            width: $barWidth;
            height:170px;
            
            &:last-child {
                margin-right: 10px;
            }
            
            .rwp-criteria-bar {
                background: #eee;
                width: $barWidth;
            }
            
            .rwp-criteria-bar-value {
                width: $barWidth;
                background: $main-color;
                -webkit-box-shadow:inset 0 3px 0 0 rgba(0, 0, 0, 0.2);
                box-shadow:inset 0 3px 0 0 rgba(0, 0, 0, 0.2);
                
                .rwp-criteria-score {
                    color: #fff;
                    width: $barWidth;
                    display: block;
                    font-weight: bold;
                    font-size: 11px;
                    text-align: center;
                    padding-top: 5px;
                }
            }
        }
    }

    // === Table Theme 5 ===
    
    &.rwp-theme-5 {
        
        .rwp-criteria-label {
            display:block;
            font-size: 14px;
            font-weight:500;
            margin-bottom: 2px;
        }
        
        .rwp-criteria-wrap {
            padding: 5px 10px;

            &:first-child {
                padding-top: 10px;
            }

            &:last-child {
                padding-bottom: 10px;
            }

            .rwp-criteria-label {
                display:block;
                font-size: 12px;
                line-height: 12px;
                font-weight:500;
                font-weight: bold;
                margin-bottom: 5px;
            }

            .rwp-bar-wrap {
                background: #eee;
            }

            .rwp-criteria-bar {
                height: 20px;
                -webkit-box-shadow:inset -3px 0 0 0 rgba(0, 0, 0, 0.2);
                box-shadow:inset -3px 0 0 0 rgba(0, 0, 0, 0.2);
            }

            .rwp-criterion-score {
                font-size: 14px;
                color: #fff;
                line-height: 20px;
                text-align: right;
                display: block;
                padding-right: 10px;
                margin-left: 10px;
            }
        }

        $iconSize: 16px;

        .rwp-str {
            @include clearfix;
            //margin-right: 120px;

            .rwp-s {
                float: left;
                display: block;
                width: ($iconSize / 2);
                height: $iconSize;
                background-image:  url(../images/rating-star.png);
                background-repeat: no-repeat;
                background-size: $iconSize;
                margin: 0;
                padding: 0;


                &.rwp-o.rwp-x {
                    background-position: 0px (-$iconSize);
                }

                &.rwp-o.rwp-f {
                    background-position: 0px 0px;
                }

                &.rwp-e.rwp-x {
                    background-position: (-($iconSize / 2)) (-$iconSize);
                }

                &.rwp-e.rwp-f {
                    background-position: (-($iconSize / 2)) 0px;
                }
            }
        }
    }
}