// Reviews Themes

// Clearfix mixin
@mixin clearfix() {
    &:before, &:after {
        content: " ";
        display: table;
    }

    &:after {
        clear: both;
    }
}
//@include clearfix;

.rwp-review-wrap {
    margin: 30px 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    
    *,
    *:before,
    *:after {
        -webkit-box-sizing: inherit;
        -moz-box-sizing: inherit;
        box-sizing: inherit;
    }

    *[hidden] { display: none; }

    .rwp-review {

        .rwp-title {
            font-size: 18px;
            font-weight: bold;
            display: block;
            margin-bottom: 20px;

            em {
                font-style: normal;
            }
        } // end title
        
        .rwp-header {
            @include clearfix;

            .rwp-overalls {
                //margin-right: 30px;
            }

            &.rwp-has-image {
                
                // *** Section: Review Image ***
                .rwp-review-image {
                    float: left;
                    overflow: hidden;
                    background: #000;
                    
                    span {
                        display: block;
                        height: 136px;
                        width: 150px;
                        background-repeat: no-repeat;
                        background-position: center;
                        background-size: cover;
                        -webkit-background-size: cover;
                        -moz-background-size: cover;
                        -o-background-size: cover;
                    } // end span

                    a.rwp-image-link {
                        @extend span;
                        border: none;
                        -webkit-transition: opacity .3s ease,-webkit-transform .3s ease;
                        -moz-transition: opacity .3s ease,-moz-transform .3s ease;
                        -o-transition: opacity .3s ease,-o-transform .3s ease;
                        transition: opacity .3s ease,transform .3s ease;
                        -webkit-backface-visibility: hidden;

                        &:hover {
                            border: none;
                            -webkit-transform: scale(1.05);
                            -moz-transform: scale(1.05);
                            -o-transform: scale(1.05);
                            transform: scale(1.05);
                            opacity: .8;
                        }
                    } // end a

                } // end review-image


                .rwp-overalls {
                    float: left;
                    min-width: 70px;
                    min-height: 68px;
                   text-transform: uppercase;

                } // end overalls

                .rwp-overall-score {
                    height: 68px;
                    //height: 54px;
                    padding: 8px;
                    text-align: center;
                    color: #fff;
                    
                    .rwp-overlall-score-value {
                        $size: 32px;
                        font-size: $size;
                        font-weight: bold;
                        display: block;
                        line-height: $size;
                    }

                    .rwp-overlall-score-label {
                        $size: 14px;
                        font-size: $size;
                        line-height: $size;
                        margin-top: 5px;
                        display: block;
                    }

                    &.rwp-only {
                        height: 136px;
                        padding: 38px 8px;
                    }

                } // end overall-score
                
                .rwp-users-score {
                    @extend .rwp-overall-score;
                    padding: 6px 8px;

                    &.rwp-ur {
                        height: 136px;
                        padding: 38px 8px;
                    }

                    .rwp-users-score-value {
                        $size: 26px;
                        font-size: $size;
                        font-weight: bold;
                        display: block;
                        line-height: $size;
                    } 

                    .rwp-users-score-label {
                        $size: 12px;
                        font-size: $size;
                        line-height: $size;
                        margin-top: 5px;
                        display: block;
                    }

                    .rwp-users-score-count {
                        $size: 10px;
                        font-size: $size;
                        line-height: $size;
                        margin-top: 1px;
                        display: block;
                        text-transform: none;
                    }

                } // end users-score

                .rwp-custom-tabs-wrap {
                    @include clearfix;
                    float: left;
                    margin-bottom: 20px;

                    .rwp-custom-tab {
                        padding: 46px 8px;
                        text-align: center;
                        color: #fff;
                        float: left;
                        display: block;
                        height: 136px;

                        span {
                            display: block;

                            &.rwp-tab_value {
                                font-size: 22px;
                                font-weight: bold;
                                line-height: 22px;
                            }

                            &.rwp-tab_label {
                                font-size: 14px;
                                line-height: 14px;
                                margin-top: 5px;
                                text-transform: uppercase;
                            }
                        }

                    } // end custom tab

                    a.rwp-custom-tab {
                        text-decoration: none;
                        border: 0;
                        margin: 0;
                    }

                } // end custom-tabs-wrap
                
                .rwp-pros-wrap, .rwp-cons-wrap {
                    //max-width: 156px;
                } // end pros-cons-wrap

            } // end has-image

            &.rwp-no-image {

                .rwp-overalls {
                    float: left;
                    min-width: 100px;
                    min-height: 90px;
                    text-transform: uppercase;
                } // end overalls

                .rwp-overall-score {
                    height: 90px;
                    padding: 12px 10px;
                    text-align: center;
                    color: #fff;
                    
                    .rwp-overlall-score-value {
                        $size: 42px;
                        font-size: $size;
                        font-weight: bold;
                        display: block;
                        line-height: $size;
                    }

                    .rwp-overlall-score-label {
                        $size: 14px;
                        font-size: $size;
                        line-height: $size;
                        margin-top: 5px;
                        display: block;
                    }

                } // end overall-score
                
                .rwp-users-score {
                    height: 42px;
                    text-align: center;
                    color: #fff;
                    padding: 2px 5px 5px 5px;

                    .rwp-users-score-value {
                        $size: 18px;
                        font-size: $size;
                        font-weight: bold;
                        line-height: $size;
                    } 

                    .rwp-users-score-label {
                        $size: 12px;
                        font-size: $size;
                        line-height: $size;
                        margin-top: 5px;
                    }

                    .rwp-users-score-count {
                        $size: 10px;
                        font-size: $size;
                        line-height: $size;
                        margin-top: 1px;
                        display: block;
                        text-transform: none;
                    }


                    &.rwp-ur {
                        min-height: 90px;
                        height: auto;
                        padding: 12px 10px;
                        text-align: center;
                        color: #fff;
        
                        .rwp-users-score-value {
                            $size: 42px;
                            font-size: $size;
                            font-weight: bold;
                            display: block;
                            line-height: $size;
                        } 

                        .rwp-users-score-label {
                            $size: 14px;
                            font-size: $size;
                            line-height: $size;
                            margin-top: 5px;
                        }

                        .rwp-users-score-count {
                            $size: 10px;
                            font-size: $size;
                            line-height: $size;
                            margin-top: 1px;
                            display: block;
                            text-transform: none;
                        }
                
                    } // end UR

                } // end users-score

                .rwp-custom-tabs-wrap {
                    @include clearfix;
                    float: left;
                    margin-bottom: 20px;

                    .rwp-custom-tab {
                        padding: 44px 10px;
                        text-align: center;
                        color: #fff;
                        float: left;
                        display: block;
                        height: 132px;

                        span {
                            display: block;

                            &.rwp-tab_value {
                                font-size: 22px;
                                font-weight: bold;
                                line-height: 22px;
                            }

                            &.rwp-tab_label {
                                font-size: 14px;
                                line-height: 14px;
                                margin-top: 5px;
                                text-transform: uppercase;
                            }
                        }

                        &.rwp-ur {
                            height: 100px;
                            padding: 30px 10px;
                        }

                    } // end custom tab

                    a.rwp-custom-tab {
                        text-decoration: none;
                        border: 0;
                        margin: 0;
                    }

                } // end custom-tabs-wrap
                
                .rwp-pros-wrap, .rwp-cons-wrap {
                    //max-width: 270px;
                } // end pros-cons-wrap

            } // end no-image

            /*.rwp-pros-and-cons {
                @include clearfix;
                float: left;
                max-width: 370px;
            }*/

            .rwp-pros-wrap {
                float: left;
                margin: 16px 0 16px 20px;
               //margin: 16px 16px 16px 20px;
                 //max-width: 160px;

               .rwp-pros {
                    display: table-cell;
               }

                .rwp-pros-label {
                    display: block;
                    font-weight: bold;
                    text-transform: uppercase;
                    margin-bottom: 5px;
                }

            } // end pros-wrap

            .rwp-cons-wrap {
                @extend .rwp-pros-wrap;
                margin-right: 0;
                margin-bottom: 0;

                .rwp-cons {
                    display: table-cell;
                }

                .rwp-cons-label {
                    display: block;
                    font-weight: bold;
                    text-transform: uppercase;
                    margin-bottom: 5px;
                }

            } // end cons-wrap

        } // end header

        .rwp-summary-wrap {
            margin-top: 20px;

            .rwp-summary-label {
                margin-bottom: 5px;
                display: block;
                font-weight: bold;
                text-transform: uppercase;
            }

        } // end summary-wrap

        .rwp-custom-links {
            margin-top: 10px;
            @include clearfix;

            a {
                text-decoration: none;
                border: none;
                display: block;
                float: left;
                margin: 0 10px 0 0 ;
                padding: 0;
                
                &:focus {
                    outline: none;
                }

                &:last-child {
                    margin: 0;
                }

                &:hover {
                    text-decoration: underline;
                }
            }

        } // end custom-link

        .rwp-scores {
            margin-top: 20px;

            .rwp-criterion {
                margin-bottom: 10px;

                &:last-child {
                    margin: 0;
                }

                .rwp-criterion-text {
                    @include clearfix;

                    .rwp-criterion-label {
                        float: left;
                        font-weight: bold;
                        margin-top: 2px;
                    }

                    .rwp-criterion-score {
                        float: right;
                        font-weight: bold;
                    }

                } // end criterion-text
                
                $barHeight: 10px;
                .rwp-criterion-bar-base {
                    background: #eee;
                    height: $barHeight;
                    margin: 5px 0 15px;

                    .rwp-score-bar {
                        //background: #56b258;
                        height: $barHeight;
                        -webkit-box-shadow: inset 0px -2px 0px 0px rgba(0,0,0,0.4);
                        box-shadow: inset 0px -2px 0px 0px rgba(0,0,0,0.4);
                    }
                }

            } // end criterion

        } // end scores

        .rwp-users-reviews-page{
             margin-bottom: 30px;

            &:last-child {
                margin: 0;
            }

            &.rwp-hidden {
                display: none;
            }
        }

        .rwp-users-reviews-wrap {
            margin-top: 30px;

            .rwp-people-label {
                margin-bottom: 20px;
                display: block;
                font-weight: bold;
                text-transform: uppercase;

                i {
                    display: inline-block;
                    color: #fff;
                    padding: 5px;
                    margin-left: 10px;
                    vertical-align: middle;
                }
                
                em {
                    float: right;
                    text-align: right;
                    text-transform: none;

                    a {
                        text-decoration: none;
                        border: none;
                        display: block;
                        margin: 0;
                        padding: 0;
                        font-style: normal;
                        
                        &:focus {
                            outline: none;
                        }

                        &:last-child {
                            margin: 0;
                        }

                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
                
            }

        } // end users-reviews-wrap
        
        .rwp-show-more {
            font-weight: bold;
            text-align: center;
            display: block;
            text-transform: uppercase;
            margin-top: 20px;

            &:hover {
                text-decoration: underline;
                cursor: pointer;
            }
        }

        .rwp-user-review {
            position: relative;
            min-height: 100px;
            margin-bottom: 30px;
            padding: 10px;

            &:last-child {
                margin: 0;
            }

            &.rwp-hidden {
                display: none;
            }

            &.rwp-highlighted {
                background-color: #f7f7f7;
            }
            
            .rwp-user-info {
                position: absolute;
                top: 10px;
                left: 10px;
                max-width: 60px;

                span {
                    display: block;
                    text-align: center;
                    font-weight: bold;
                    margin-top: 6px;
                    width: 70px;
                    word-wrap: break-word;  
                }
            } // end user-info

            .rwp-ur-content {
                margin-left: 80px;
                min-height: 80px;

                &.rwp-no-avatar {
                    margin-left: 0;
                }
                
            } // end user-review-content 

            .rwp-ur-title {
                display: block;
                font-weight: bold;
                margin-bottom: 10px;
            } // end ur-title

            .rwp-ur-message {
                margin-bottom: 6px;
            } // end ur-message
            
            .rwp-ur-date {
                margin-bottom: 10px;
                display: block;
            }

            .rwp-ur-ratings-wrap {
                position: relative;
            } // end ur-ratings

            .rwp-ur-ratings {
                @include clearfix;
                margin-right: 120px;
            }

            .rwp-criterion {
                margin: 0 20px 10px 0;
                width: 150px;
                float: left;

                .rwp-str {
                    margin-top: 2px;
                }

                &:last-child {
                    margin: 0;
                }

                .rwp-criterion-text {
                    @include clearfix;

                    .rwp-criterion-label {
                        float: left;
                        font-weight: bold;
                        //margin-top: 2px;
                    }

                    .rwp-criterion-score {
                        float: right;
                        font-weight: bold;
                    }

                } // end criterion-text
                
                $barHeight: 6px;
                .rwp-criterion-bar-base {
                    background: #eee;
                    height: $barHeight;
                    margin: 3px 0 0 0;

                    .rwp-score-bar {
                        //background: #56b258;
                        height: $barHeight;
                    }
                }

            } // end criterion
            
            .rwp-ur-like-wrap {
                @include clearfix;
                width: 110px;
                position: absolute;
                top: 0;
                right: 0;
                
                .rwp-dislike {
                    display: block;
                    float: right;

                    em {
                        font-size: 12px;
                        line-height: 12px;
                        font-weight: bold;
                        color: #e74343;
                        display: inline-block;
                        vertical-align: top;
                        margin-top: 6px;
                        font-style: normal;
                    }
                    
                    span {
                        //margin-left: 2px;
                        
                        width: 18px;
                        height: 24px;
                        display: inline-block;
                        vertical-align: top;
                        background-image: url(../images/rwp-like.png);
                        background-repeat: no-repeat;
                        background-size: 36px;
                        background-position: 0px 0px;

                        &:hover {
                            cursor: pointer;
                        }
                    }
                }

                .rwp-like {
                    @extend .rwp-dislike;

                    em {
                        color: #56b258;
                    }
                    
                    span {
                        background-position: -18px -0px;
                        margin-right: 10px;
                    }
                }

                .rwp-loader {
                    @extend .rwp-dislike;
                    width: 16px;
                    height: 16px;
                    background: url(../images/loader-2.gif);
                    margin: 4px 6px 0 0;
                    display: none;
                }

            } // end ur-like-wrap / on

            .rwp-ur-like-wrap.rwp-off {
                @extend .rwp-ur-like-wrap;

                em { color: #a5a5a5; }

                .rwp-dislike span {
                    background-position: 0px -24px;

                    &:hover {
                        cursor: auto;
                    }
                }

                .rwp-like span {
                    background-position: -18px -24px;

                    &:hover {
                        cursor: auto;
                    }
                }

            } // end ur-like-wrap / off

            .rwp-ur-sharing {
                margin-top: 16px;
            }
            .rwp-share {
              display: inline-block;
              vertical-align: middle;
              width: 20px;
              height: 20px;
              border: none !important;
              margin-right: 10px;
            }
            .rwp-share:last-child {
              margin-right: 0px;
            }

            .rwp-share:before {
              display: inline-block;
              width: 20px;
              height: 20px;
              font-size: 20px;
              line-height: 1;
              font-family: dashicons;
              text-decoration: inherit;
              font-weight: 400;
              font-style: normal;
              vertical-align: top;
              text-align: center;
              color: #a5a5a5;
            }
            .rwp-share.rwp-facebook:before {
              //color: #3B5998;
              content: "\f305";
            }
            .rwp-share.rwp-twitter:before {
              //color: #55ACEE;
              content: "\f301";
            }
            .rwp-share.rwp-google:before {
              //color: #dd4b39;
              content: "\f462";
            }
            .rwp-share.rwp-email:before {
              //color: #666;
              content: "\f465";
            }
            .rwp-share.rwp-sharing-link:before {
              //color: #666;
              content: "\f103";
            }

        } // end user-review

        .rwp-ratings-form-wrap {
            margin-top: 30px;

            .rwp-ratings-form-label {
                margin-bottom: 20px;
                display: block;
                font-weight: bold;
                text-transform: uppercase;
            }

        } // end users-reviews-wrap


        .rwp-ratings-form {
            position: relative;

            p {
                margin-bottom: 10px;
            }

            .rwp-rating-form-user-info {
                position: absolute;
                top: 0;
                left: 0;

                img {   
                    width: 50px;
                    height: 50px;
                    float: none;

                    border-radius: 50%;
                    -webkit-border-radius: 50%;
                }

                span {
                    display: block;
                    text-align: center;
                    font-weight: bold;
                    margin-top: 6px;
                    width: 70px;
                    word-wrap: break-word; 
                }
            } // end user-info

            .rwp-rating-form-content {
                margin-left: 80px;
                min-height: 80px;

                &.rwp-no-avatar {
                    margin-left: 0;
                }

                input[type=text], textarea {
                    padding: 6px;
                    margin: 0;
                    background: #fff;
                    border: 1px solid rgba(51, 51, 51, 0.1);
                    width: 100%;
                }

                textarea {
                    min-height: 80px;
                }

                .rwp-error-msg {
                    font-size: 12px;
                    color: #e74343;
                    padding-left: 10px;
                    display: none;
                }

                .rwp-scores-sliders {
                    @include clearfix;
                    list-style: none;
                    margin: 0 0 20px 0;
                    padding: 0;

                    li {
                        float: left;
                        margin-right: 10px;
                        margin-bottom: 10px;
                        list-style: none;

                        &:last-child {
                            margin: 0;
                        }
                    }

                    label {
                        display: block;
                    }

                    &.rwp-with-stars {
                        label.rwp-lab {
                            padding-left: 12px;
                        }

                        .rwp-stars {
                            margin-bottom: 0;
                        }
                    }

                    input[type=text] {
                        width: 50px !important;
                        padding: 4px;
                        display: inline-block;
                        vertical-align: middle;
                        margin-right: 10px;
                    }

                    .rwp-slider {
                        width: 190px;
                        display: inline-block;
                        vertical-align: middle;
                    }

                } //  end ratings-sliders

                p.rwp-submit-wrap {
                    display: block;
                    @include clearfix;

                    .rwp-notification {
                        margin-right: 10px;
                    }
                }
                
                .rwp-loader {
                    float: right;
                    width: 16px;
                    height: 16px;
                    background: url(../images/loader-2.gif);
                    margin: 7px 6px 0 0;
                    display: none;
                }

                input[type=button] {
                    padding: 6px 8px;
                    float: right;
                    margin: 0;
                    font-size: 14px;
                    border: none;
                    width: auto;
                    color: #fff;
                    text-transform: uppercase;
                    font-weight: bold;
                }

                .rwp-captcha-image {
                    display: inline-block;
                    vertical-align: middle;
                    /*text-align: center;
                    background: #404040;
                    color: #fff;
                    width: 80px;
                    height: 33px;
                    line-height: 33px;
                    font-size: 24px;*/
                }

                input[type=text].rwp-captcha-input {
                    display: inline-block;
                    width: 70px;
                    vertical-align: middle;
                    margin-left: 10px;
                }

                .rwp-refresh-captcha-btn {
                    width: 18px;
                    height: 18px;
                    color: #404040;
                    display: inline-block;
                    vertical-align: middle;
                    margin-left: 10px;
                    background-image:  url(../images/refresh.png);
                    background-repeat: no-repeat;
                    background-size: 18px;

                    &:hover {
                        cursor: pointer;
                    }
                }

            } // end rwp-rating-form-content

        } // end ratings-form

    } // end review

    p {
        margin: 0;
        padding: 0;
    }
    
    $iconSize: 20px;

    .rwp-stars {
        @include clearfix;
        width: ($iconSize * 5) + ($iconSize / 2) + 2;
        margin-bottom: 20px;

        input[type=radio] {
            display: none;
        }

        .rwp-star {
            float: right;
            display: block;
            width: ($iconSize / 2);
            height: $iconSize;
            background-image:  url(../images/rating-star.png);
            background-repeat: no-repeat;
            background-size: $iconSize;
            margin: 0;
            padding: 0;

            &.rwp-even {
                background-position: -($iconSize / 2) (-$iconSize);
            }
            
            &.rwp-odd {
                background-position: 0px (-$iconSize);   
            }

            &:last-child{
                background: none;
            }
        }

        .rwp-star:hover, .rwp-star:hover ~ .rwp-star, .rwp-rating:checked ~ .rwp-star {
          background-position: 0px 0px;
        }

        .rwp-star.rwp-even:hover,.rwp-star:hover ~ .rwp-star.rwp-even, .rwp-rating:checked ~ .rwp-star.rwp-even {
          background-position: -($iconSize / 2) 0px;
        }
    } // end stars 

    $iconSize: 16px;

    .rwp-str {
        @include clearfix;
        //margin-right: 120px;

        .rwp-s {
            float: left;
            display: block;
            width: ($iconSize / 2);
            height: $iconSize;
            background-image:  url(../images/rating-star.png);
            background-repeat: no-repeat;
            background-size: $iconSize;
            margin: 0;
            padding: 0;


            &.rwp-o.rwp-x {
                background-position: 0px (-$iconSize);
            }

            &.rwp-o.rwp-f {
                background-position: 0px 0px;
            }

            &.rwp-e.rwp-x {
                background-position: (-($iconSize / 2)) (-$iconSize);
            }

            &.rwp-e.rwp-f {
                background-position: (-($iconSize / 2)) 0px;
            }

            /*&.rwp-o {
                background-position-x: 0px ;//background-position: -($iconSize / 2) (-$iconSize);
            }
            
            &.rwp-e {
                background-position-x: -($iconSize / 2);   
            }

            &.rwp-f{
                background-position-y: 0px; //background-position: 0px 0px;
            }

            &.rwp-x {
                background-position-y: (-$iconSize); //background-position: -($iconSize / 2) 0px;
            }*/
        }
    }

    &.rwp-theme-2 {
        
        .rwp-scores {

            .rwp-criterion {
                
            }
            
            .rwp-criterion-label {
                font-weight: bold;
                min-width: 100px;
