                //background: #56b258;
                height: $barHeight;
                -webkit-box-shadow: inset 0px -2px 0px 0px rgba(0,0,0,0.4);
                box-shadow: inset 0px -2px 0px 0px rgba(0,0,0,0.4);
            }
        }

    } // end criterion
    
    .rwp-w-comment {
        font-size: $fontSize;
        display: block;
        text-align: justify;
        margin: 0;
        padding: 0;
    }

    .rwp-w-title {
        @extend .rwp-w-comment;
        font-weight: bold;
        margin-bottom: 5px ;
    }

    .rwp-w-post-title {
        @extend .rwp-w-comment;
        margin-bottom: 10px;
        font-style: italic;
        padding-bottom: 2px;
        border-bottom: 1px solid #ccc;
    }

    .rwp-ranking-number {
        font-size: 18px;
        line-height: 18px;
        font-weight: bold;
        display: block;
        position: absolute;
        left: 0;
        top: 14px;
    }

    li.rwp-has-ranking .rwp-wdj-content { 
        margin-left: 25px;
    }

    a {
        font-size: $fontSize;
        margin-top: 10px;

    }

} // end widget-ratings

// === Reviews List Shortcode ===

.rwp-rl-wrapper {

    &.rwp-rl-inline {
        display: inline-block;
        margin-right: 20px;
    }
    
    .rwp-rl-title {
        font-size: 18px; 
        font-weight: bold;
        display: block;
    }
    
    .rwp-rl-review {
        min-height: 100px;
        padding: 10px 0 10px 0;
        border-bottom: 1px solid #e5e5e5;

        &.rwp-rl-no-image .rwp-rl-ranking {
            margin-right: 149px;
        }
        
        .rwp-rl-ranking {
            font-size: 26px;
            line-height: 26px;
            font-weight: bold;
            display: inline-block;
            vertical-align: middle;
            margin: 0 16px 0 5px;
        }

        .rwp-rl-img-link {
            text-decoration: none;
            display: inline-block;
            vertical-align: middle;
            margin-right: 16px;

            border: none;
            text-decoration: none;

            &:hover {
                border: none;
                text-decoration: none;
            }

            .rwp-rl-review-image {
                height: 100px;
                width: 113px;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center;
                position: relative;
            }
        }

        .rwp-rl-review-info {
            display: inline-block;
            vertical-align: middle;
        }

        .rwp-rl-review-title {
            font-size: 16px;
            line-height: 16px;
            font-weight: bold;
            text-decoration: none;
            color: #323232;
            margin-bottom: 20px;
            display: block;
            border: none;
            text-decoration: none;

            &:hover {
                border: none;
                text-decoration: underline;
            }
        }

        .rwp-rl-total-score-wrap {
            display: inline-block;
            vertical-align: top;

            .rwp-rl-total-score-label {
                display: block;
                font-size: 14px;
                line-height: 14px;
                //font-weight: bold;
                margin-bottom: 10px;
            }

            .rwp-rl-total-score {
                font-size: 30px;
                line-height: 30px;
                //font-weight: bold;
            }

            &.rwp-rl-tab {
                margin-left: 20px;

                a {
                    text-decoration: none;
                    border: none;
                }
            }
        }

        .rwp-rl-users-score-wrap {
            @extend .rwp-rl-total-score-wrap;
            margin-left: 20px;

            .rwp-rl-users-score-label {
                @extend .rwp-rl-total-score-label;
            }

            .rwp-rl-users-score {
                @extend .rwp-rl-total-score;

                em {
                    font-size: 12px;
                    font-style: normal;
                    font-weight: normal;
                }
            }
        }

        $iconSize: 20px;

        .rwp-str {
            @include clearfix;
            //margin:  0 0 0 20px;
            display: inline-block;
            vertical-align: middle;

            .rwp-s {
                float: left;
                display: block;
                width: ($iconSize / 2);
                height: $iconSize;
                background-image:  url(../images/rating-star.png);
                background-repeat: no-repeat;
                background-size: $iconSize;
                margin: 0;
                padding: 0;

                &.rwp-o.rwp-x {
                    background-position: 0px (-$iconSize);
                }

                &.rwp-o.rwp-f {
                    background-position: 0px 0px;
                }

                &.rwp-e.rwp-x {
                    background-position: (-($iconSize / 2)) (-$iconSize);
                }

                &.rwp-e.rwp-f {
                    background-position: (-($iconSize / 2)) 0px;
                }
            }
        } // end str

    }
} // end reviews-list

// Components
.rwp-rating-stars {
    $size: 24px;
    width: $size * 5;
    height: $size;
    background-size: $size;
    background-position: 0 (-$size);

    div {
        background-position: 0 0;
        background-size: $size;
        background-image: inherit;
        display: block;
        height: $size;
    }   
}
.rwp-rating-stars-count {
    font-size: 14px;
    margin-top: 3px;
}


